import {
	mapState,
	mapActions,
	mapGetters,
	mapMutations
} from 'vuex'

import {
	uniList,
	uniListItem,
} from "@dcloudio/uni-ui"

export default {
	name: "TaskList",
	computed: {
		...mapState('home', [
			'taskList',
		])
	},

	components: {
		uniList,
		uniListItem
	},

	methods: {
		...mapActions('home',['getTaskDetailsAction']),
		...mapGetters('authed', [
			'dontTokens',
		]),
		getImage: function(imgUrl) {
			if (imgUrl && imgUrl.length > 0) {
				return `http://oss.workai.com.cn/public/${JSON.parse(imgUrl[0]).object}`
			} else {
				return `../../static/img/pic.png`
			}
		},
		linkTaskDetails: function(e) {
			uni.navigateTo({
			    url: `taskDetails?id=${e.currentTarget.id}`
			});
		},
		
		ontapTask: function (e) {
			console.log(e)
		}
	}
}
